<template>
  <div id="app">
    <v-header></v-header>
    <v-nav></v-nav>
    <div class="register">
      <p class="login-title">用户注册</p>
      <div class="login-input-list">
        <div class="login-input">
          <el-input placeholder="请输入用户名">
            <template slot="prepend">用户名：</template>
          </el-input>
          <span class="tips">用户名为2~16位字母，数字，符号或中文</span>
        </div>
        <div class="login-input">
          <el-input placeholder="请输入密码">
            <template slot="prepend">密 码：</template>
          </el-input>
          <span class="tips">密码为6~16位字符组成,采用数字、字母、符号安全性更高</span>
        </div>
        <div class="login-input">
          <el-input placeholder="请确认密码">
            <template slot="prepend">确认密码：</template>
          </el-input>
          <span class="tips">请再次填写密码</span>
        </div>
        <p>
          <button>同意协议并注册</button>
          <span class="message"><a href="/module/login.html"> 已有账号，马上登录</a></span>
        </p>
        <p class="book"><a href="###">《使用协议说明书》</a></p>
      </div>
    </div>
    <v-footer></v-footer>
  </div>
</template>

<script>
  import header from 'components/header/header.vue';
  import nav from './components/nav/nav.vue';
  import footer from 'components/footer/footer.vue';
  export default {
    data(){
      return {}
    },
    components: {
      'v-header': header,
      'v-nav': nav,
      'v-footer': footer
    }
  }
</script>

<style>
  * {
    margin: 0;
    padding: 0;
  }

  ul {
    list-style: none;
  }

  a {
    text-decoration: none;
  }

  .register {
    margin: 0 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #999;
  }

  .login-input {
    width: 400px;
    margin-bottom: 40px;
  }

  .login-title {
    align-self: flex-start;
    border-bottom: 1px solid #ddd;
    width: 100%;
    font: 14px/40px "微软雅黑";
    padding-left: 20px;
  }

  .login-input-list {
    padding: 50px 0 100px 0;
  }

  .login-input-list p {
    text-align: center;
  }

  button {
    color: #fff;
    background-color: #5cb85c;
    border-color: #4cae4c;
    display: inline-block;
    padding: 6px 12px;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.42857143;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    cursor: pointer;
    user-select: none;
    border: 1px solid transparent;
    border-radius: 4px;
  }

  .message {
    padding-left: 40px;
  }

  .tips {
    padding-left: 70px;
    padding-top: 20px;
    margin-bottom: 10px;
    color: #737373;
    font-size: 14px;
  }
  .book{padding-top: 20px;}
</style>



